.top-line {
    position: relative;
    flex-wrap: wrap;
    align-items: flex-start;
    @include flex;

    @include md-block() {
        // margin-bottom: 20px;
        margin-bottom: 0;
    }

    @include xs-block() {
        margin: 5px 0;
    }

    &__inner {
        @include flexCenter;
        // flex-grow: 1;

        @include xs-block() {
            width: 100%;
        }

        .top-line__date {
            margin-bottom: 0;
        }
    } //.top-line__inner

    &__sorting {
        position: relative;
        flex-grow: 1;
        padding: 10px;

        .control-box {
            @include xs-block() {
            }

            @include xs-block() {
                margin-top: 10px;
            }
        } //.top-line__sorting .control-box

        .btn--back {
            height: 40px;
            line-height: 40px;
        } //.top-line__sorting .btn--back

        .button-box {
            justify-content: space-between;
            margin-left: auto;

            @include lg-block() {
                // margin-top: 20px;
                margin-left: 0;
            }

            .btn {
                width: 48%;
                min-width: 200px;

                & + .btn {
                    margin-left: 12px;
                }
            }
        } //.top-line__sorting .button-box

        .search__input {
            width: 100%;
        }
    } //.top-line__sorting

    &__button-box {
        flex-shrink: 0;
        margin-bottom: 20px;
        margin-left: 20px;

        &.ml-a {
            margin-left: auto;
        } //.top-line__button-box.ml-a
    } //.top-line__button-box

    &__studio-select {
        min-width: 300px;

        .bb-input {
            width: 100%;
        }
    } //.top-line__studio-select

    &__search {
        width: 100%;
        // margin-right: $gutter * 2;

        // @include md-block() {
        //     width: 50%;
        //     margin-bottom: $gutter;
        //     margin-right: $gutter;
        // }

        // @include xs-block() {
        //     width: calc(100% - 40px);
        //     margin-bottom: $gutter / 2;
        //     margin-right: 40px;
        // }

        // @include max(i5) {
        //     margin-right: 0;
        // }
        max-width: 320px;
        @include flexCenter;

        @include sm-block() {
            flex-grow: 1;
            max-width: initial;
        }

        // @include xs-block() {
        //     padding: 0;
        // }

        @include landscape() {
            max-width: 215px;
        }

        &.pos-xs-1 {
            margin-left: 20px;

            @include xs-block() {
                margin-left: 0;
            }
        }

        .search {
            max-width: 100%;
            height: 40px;

            @include xs-block() {
                width: 100%;
            }

            @include max(i5) {
                margin: 0;
            }

            &__input {
                @extend %styleInput;

                @include xs-block() {
                    width: 100%;
                }

                &:hover {
                    border: 1px solid $input-color-border--hover;
                }

                &:focus {
                    border: 1px solid darken($input-color-border--hover, 10%);
                    font-size: 16px;
                    // box-shadow: $input-shadow--hover;
                }
            } //.top-line__search .search__input
        }

        .bb-input {
            flex-grow: 1;
            width: 100%;
        }

        &--left {
            align-items: flex-start;
            justify-content: flex-start;
            margin-right: 20px;
            margin-left: 0 !important;
        }
    } //.top-line__search

    &__date {
        // margin-left: $gutter;

        // @include md-block() {
        //     // margin-bottom: 20px;
        //     // margin-bottom: 0;
        // }

        @include xs-block() {
            width: 100%;
            margin: 0;
            margin-bottom: 10px;
            @include flexCenter;
        }

        & + .top-line__select {
            margin-left: 20px;

            @include xs-block() {
                margin-left: 0;
            }
        } //.top-line__date + .top-line__date
    } //.top-line__date

    &__select {
        @include xs-block() {
            // width: calc(50% - 5px);
            width: 100%;
            // margin-top: 10px;
            margin-right: 0;
            margin-bottom: 0;

            &:last-child {
                margin-bottom: 0;
            }

            & + & {
                margin-top: 10px;
                margin-left: 0;
            }

            .bb-select {
                width: 100%;
            }
        }

        @include max(i5) {
            width: 100%;

            & + & {
                margin-left: 0;
            }
        }

        & + & {
            margin-left: $gutter;
        } //.top-line__select +  //.top-line__select

        & + .top-line__date {
            margin-left: 20px;

            @include xs-block() {
                margin-left: 0;
            }
        } //.top-line__select + .top-line__date

        &.w300 {
            min-width: 300px;
            max-width: 300px;

            @include xs-block() {
                min-width: 100%;
                max-width: 100%;
            }
        } //.top-line__select.w300

        .bb-input--select {
            width: 100%;
            // min-width: 230px;

            @include xs-block() {
                min-width: 100%;
                background-color: #fff;
            }
        } //.top-line__select .bb-input--select
    } //.top-line__select

    &__select-box {
        @include flexCenter;

        .top-line__select {
            margin-bottom: 0;

            @include xs-block() {
                &:not(:last-child) {
                    select {
                        border-right: 0;
                    }
                }

                &:first-child {
                    select {
                        border-top-left-radius: $border-radius;
                        border-bottom-left-radius: $border-radius;
                    }
                }

                &:last-child {
                    select {
                        border-top-right-radius: $border-radius;
                        border-bottom-right-radius: $border-radius;
                    }
                }

                select {
                    border-radius: 0;
                }
            }

            & + .top-line__select {
                @include xs-block() {
                    margin-left: 0;
                }
            }
        }
    } //.top-line__select-box

    &__btn {
        flex: none;
        margin-left: auto;

        @include md-block() {
            padding: 7px 15px;
        }

        &_controls {
            display: flex;
            flex-shrink: 0;
            width: 40px;
            height: 40px;
            margin-left: 7px;
            padding: 7px;
            border: 1px solid $input-color-border;
            border-radius: $border-radius;
            background-color: #fff;
            line-height: 1;
            // position: absolute;
            // top: 6px;
            // top: 0;
            // right: 0;

            @include landscape {
                // display: flex !important;
            }

            @include xs-block() {
                display: flex;
            }
        } //.top-line__btn_controls
    } //.top-line__btn

    &__box {
        @include flexCenter;
    } //.top-line__box

    &--expanded {
        margin-bottom: 10px;

        @include xs-block() {
            margin-bottom: 10px;
        }
    } //.top-line--expanded

    &--block {
        display: block;
    } //.top-line--block

    &--offset {
        margin-bottom: 20px;
    } //.top-line--offset

    .select2-selection__rendered {
        // max-width: 240px;
        @include text-overflow;

        @include xs-block() {
            max-width: 100%;
        }
    } //.top-line .select2-selection__rendered
} //.top-line
