.day-picker {
    width: 100%;
    // padding-bottom: 18px;
    // border-bottom: 1px solid $color-border--lightest;
    background-color: #fff;

    @include min(md) {
        // margin-bottom: 20px;
        padding-bottom: 0;
        border-bottom: 0;
    }

    &__top {
        margin-bottom: 10px;
        padding: 0 20px;
        color: $gray--darkest;
        text-align: center;
        @include blockHeight;

        @include xs-block() {
            padding: 0 10px;
        }

        &:hover {
            cursor: default;
        }
    } //.day-picker__top

    &__carousel {
        position: relative;
        display: flex;
        align-items: center;
    } //.day-picker__carousel

    &__item {
        position: relative;
        width: 14%;
        transition: top 0.35s ease;
        text-align: center;
        cursor: default;

        @include min(md) {
            width: 60px;
            margin: 0 12px;
        }

        &:hover:not(.is-active):not(.is-disabled) {
            cursor: pointer;

            .day-picker__day_num {
                background-color: darken(#fff, 3%);
            }
        }

        &.is-active {
            .day-picker__day_num {
                background-color: #000;
                color: #fff;
            }
        }

        &.is-disabled {
            .day-picker__day {
                &_title {
                    color: $color-text--disabled;
                }

                &_num {
                    color: $color-text--disabled;
                }
            }
        }
    } //.day-picker__item

    &__day {
        &_title {
            margin-bottom: 3px;

            font-size: 11px;
            text-transform: uppercase;
        } //.day-picker__day_title

        &_num {
            display: flex;
            align-items: center;
            justify-content: center;
            // width: 40px;
            // height: 40px;
            margin: 0 auto;
            transition: 0.2s;
            border-radius: 50%;
            color: $color-text--darkest;
            font-size: 22px;
        } //.day-picker__day_num
    } //.day-picker__day

    &__week {
        font-weight: 300;
    } //.day-picker__week

    &__arrow {
        position: absolute;
        top: -83%;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 30px;
        height: 30px;
        border: 0;
        outline: none;
        background-color: transparent;
        color: $color-accent;
        font-size: 20px;

        @include min(md) {
            position: relative;
            display: flex;
            align-items: center;
            justify-content: center;
            width: 50px;
            height: 50px;
            border-radius: 50%;
        }

        &:hover:not(.is-disabled) {
            @include min(md) {
                // background-color: $color-accent;
                color: #fff;
                cursor: pointer;
                @include tr;
            }
        }

        i {
            padding-top: 2px;
            font-weight: 500;
        }

        &--left {
            left: 0;

            i {
                padding-right: 3px;
            }

            svg {
                transform: rotate(180deg);
            }
        } //.day-picker__arrow-long--left

        &--right {
            right: 0;

            i {
                padding-left: 3px;
            }
        } //.day-picker__arrow--right

        &.is-disabled {
            color: $color-icon--light;
            cursor: default;
        }

        svg {
            width: 18px;
            height: 18px;
            fill: $color-icon--darken;
        } //.day-picker__arrow svg
    } //.day-picker__arrow

    &__list {
        display: flex;
        flex: 1;
        justify-content: center;
        width: 100%;
        padding-bottom: 20px;
    } //.day-picker__list

    &.is-fixed {
        position: fixed;
        top: 50px;
        left: 0;
        z-index: z(fixed);
        width: 100%;
        box-shadow: $bxs;
    } //.day-picker.is-fixed

    &--short {
        .day-picker {
            &__item {
                &:hover:not(.is-active):not(.is-disabled) {
                    .day-picker__day_num {
                        background-color: transparent;
                    }
                }

                &.is-active {
                    // @include tY(-12px);
                    top: -12px;

                    .day-picker {
                        &__day {
                            &_num {
                                background-color: transparent;
                                color: $color-text--darkest;
                                font-size: 42px;
                                // width: 50px;
                                // height: 50px;
                                font-weight: 600;
                                line-height: 1.2;
                            } //.day-picker--short .day-picker__item.is-active__day_num

                            &_title {
                                font-size: 13px;
                            } //.day-picker--short .day-picker__item.is-active__day_title
                        } //.day-picker--short .day-picker__item.is-active__day
                    }
                } //.day-picker--short .day-picker__item.is-active
            } //.day-picker--short .day-picker__item

            &__day {
                &_num {
                    transition: initial;
                } //.day-picker--short .day-picker__day_num
            } //.day-picker--short .day-picker__day
        }
    } //.day-picker--short
} //.day-picker
