.time-picker {
    overflow-x: hidden;

    &__wrap {
        flex: 1;
        width: 100%;

        @include min(md) {
            max-height: 276px;
            overflow: auto;
            border: 1px solid $color-border--lightest;
        }

        &.is-empty {
            border-right: 0;
            border-bottom: 0;
            border-left: 0;

            .time-picker {
                &__empty {
                    display: flex !important;
                }
            }
        }
    } //.time-picker__wrap

    &__item {
        position: relative;
        justify-content: center;
        width: 100%;
        padding: 15px 20px;
        color: $color-text--darkest;
        cursor: pointer;
        @include flex;

        @include xs-block() {
            justify-content: center;
            padding: 15px;
            text-align: center;
        }

        &::after {
            content: '';
            right: 18px;
            display: inline-block;
            width: 25px;
            height: 25px;
            background: url('../files/sprite.svg/#angle') top left no-repeat;
            // @include s('right-arrow');
            // color: $color-accent;
            // @include fa;
            // content: '\f105';
            font-size: 20px;
            @include centre('y');

            @include min(md) {
                display: none;
            }

            @include xs-block() {
                right: 10px;
            }
        }

        &:hover {
            @include min(md) {
                border-color: transparent;
                box-shadow: inset 0 0 0 2px $color-accent;
            }
        }

        & + .time-picker__item {
            border-top: 1px solid $color-border--lightest;
        }

        &.is-disabled {
            color: $gray--lighten;

            &::after {
                display: none;
            }

            &:hover {
                box-shadow: none;
            }

            .time-picker {
                &__title {
                    padding-right: 6px;
                    color: $color-text--lighten;
                } //.time-picker__item.is-disabled .time-picker__title

                &__icon {
                    display: none;
                } //.time-picker__item.is-disabled .time-picker__icon
            } //.time-picker__item.is-disabled .time-picker
        } //.time-picker__item.is-disabled

        &.is-active {
            border-color: transparent;
            box-shadow: inset 0 0 0 2px $color-accent;
        } //.time-picker__item.is-active
    } //.time-picker__item

    &__icon {
        right: 10px;
        width: 15px;
        height: 15px;
        @include centre('y');
    } //.time-picker__icon

    &__empty {
        display: none !important;
        align-items: center;
        justify-content: center;
        width: 100%;
        height: 274px;
        color: $color-text;
    } //.time-picker__empty

    &__time-box {
        padding: 0 15px 15px;
        background-color: #fff;
        @include row-flex;
    } //.time-picker__time-box

    &__time-btn {
        justify-content: center;
        margin-top: 15px;
        // padding: 8px 15px 7px;

        font-size: 15px;
        text-align: center;
        // @include border(all);
        @include col;
        @include size(3);

        &.is-checked {
            // border: 0;
            // background-color: $gray;
            // color: #fff;
        } //.time-picker__time-btn .is-checked
    } //.time-picker__time-btn

    a {
        transition: none;
    } //.time-picker a
} //.time-picker

.modal-body {
    .day-picker {
        @include border(top);
        @include border(bottom);
    }
}
