$card-indent: 50px;
$card-indent--mobile: 10px;

.card-services {
    margin-bottom: $card-indent;

    @include sm-block() {
        margin-bottom: $card-indent--mobile;
    }

    &__items {
        .bb-accordeon {
            &__item {
                & + .bb-accordeon__item {
                    margin-top: 7px;
                }
            } //.card-services__items .bb-accordeon__item

            &__title {
                @include max(i7) {
                    padding: 15px 10px;
                }

                &::before {
                    margin-right: 15px;
                }
            } //.card-services__items .bb-accordeon__title
        } //.card-services__items .bb-accordeon
    } //.card-services__items

    &__title {
        @extend .bb-accordeon__title;
    } //.card-services__title

    .page-title {
        &__wrap {
            @include sm-block() {
                display: none;
            }
        }

        &--small {
            @include sm-block() {
                display: none;
            }
        }
    } //.card-services .page-title

    .bb-accordeon {
        &__item {
            // border: 0;
            // box-shadow: none;
            // border-top: 1px solid $color-border--lightest;
            // border-left: 1px solid $color-border--lightest;
            // border-right: 1px solid $color-border--lightest;

            @include xs-block() {
                border: 0;
            }
        } //.card-services .bb-accordeon__item

        &__title {
            @include xs-block() {
                padding: 18px 10px;
            }
        } //.card-services .bb-accordeon__title
    } //.card-services .bb-accordeon
} //.card-services

@import './layout/_service-item';
