.order {
    width: 100%;

    &__inner {
        position: relative;
        display: flex;
        justify-content: center;
        @extend .container;

        @include sm-block() {
            padding-right: 0;
            padding-left: 0;
        }

        @include min(md) {
            justify-content: space-between;
        }
    } //.order__inner

    &__title {
        flex: 1;
        justify-content: center;
        padding-right: 20px;
        color: #fff;

        @include flexCenter;

        @include min(md) {
            color: $gray--darkest;
            font-size: 20px;
        }
    } //.order__title

    &__top-line {
        position: fixed;
        top: 0;
        z-index: 110;
        width: 100%;
        padding: 0 20px;
        background-color: $color-bg--dark;
        @include blockHeight;
        @include flexCenter;

        @include min(md) {
            position: relative;
            margin-bottom: 30px;
            padding: 11.5px 20px;
            border-bottom: 1px solid $color-border--lightest;
            background-color: #fff;
        }

        @include xs-block() {
            height: 50px;
            padding: 0 10px;
            line-height: 50px;
        }

        .btn-link {
            left: 0;
            @include centre('y');

            &:hover {
                text-decoration: none;
            }

            i {
                margin-top: 0;
                font-size: 20px;
            }

            span {
                @include xs-block() {
                    display: none;
                }
            }
        } //.order__top-line .btn-link
    } //.order__top-line

    &__left {
        width: 100%;
        @include flexColumn;

        @include xs-block() {
            height: 100%;
        }

        @include min(md) {
            width: 65%;
            margin-right: 30px;
        }

        &_top {
            position: absolute;
            top: 0;
            left: 0;
            z-index: 20;
            display: flex;
            align-items: center;
            justify-content: center;
            width: 100%;
            padding: 10px;
            border-bottom: 1px solid $color-border--lightest;
            background-color: #fff;

            @include min(md) {
                position: relative;
                display: flex;
                width: 100%;
                margin: 0;
                margin-bottom: 30px;
                padding: 0;
                border-bottom: 0;
            }
        } //.order__left_top
    } //.order__left

    &__right {
        position: fixed;
        bottom: 0;
        left: 0;
        width: 100%;
        background-color: #fff;

        @include min(md) {
            position: relative;
            top: 0;
            width: 32%;
        }
    } //.order__right

    &__day-picker {
        position: static;
        box-shadow: none;
        // @include xs-block() {
        //     position: sticky;
        //     top: 0;
        //     left: 0;
        //     z-index: z(dropdown);
        //     width: 100%;
        // }
    } //.order__day-picker

    &--info {
        padding-top: 0;

        @include min(md) {
            padding-top: 30px;
        }
    } //.order--info

    .btn-icon {
        padding: 0;
        background-color: transparent;

        &:hover {
            cursor: pointer;
        } //.icon:hover

        &--delete {
            position: absolute;
            top: 50%;
            left: 70px;
            width: 20px;
            height: 20px;
            overflow: hidden;
            transform: translateY(-50%);
            @include tr;

            .fa-minus-circle {
                color: $color-accent;
                font-size: 20px;
            }
        } //.icon--delete
    } //.order .icon

