.confirm-msg {
    display: flex;
    align-items: center;
    width: 100%;
    padding: 10px;
    border: 1px solid #d7eacf;
    background-color: #f4f9f2;
    font-size: 15px;

    @include min(md) {
        width: 100%;
        margin: 0;
    }

    &:hover {
        cursor: default;
    }

    &__icon {
        margin: 0 20px 0 10px;

        i {
            color: #6fb557;
            font-size: 40px;
        }
    } //.confirm-msg__icon

    &__title {
        font-weight: 500;

        @include min(md) {
            margin-bottom: 5px;
        }
    } //.confirm-msg__title
} //.confirm-msg

.user-info {
    width: 100%;
    // @extend %boxLight;
    border: 1px solid $color-border--lightest;
    @include flexColumn;

    @include sm-block() {
        border: 0;
        box-shadow: none;
    }

    &__inner {
        z-index: 10;
        width: 100%;
        transition: margin-left 0.5s;
        background-color: #fff;

        @include sm-block() {
            padding: 0;
        }
    } //.user-info__inner

    &__body {
        flex: 1;
    }

    &__wrap-scroll {
        max-height: 400px;
        overflow: auto;
    } //.user-info__wrap-scroll

    &__top {
        position: relative;
        padding: 20px;
        overflow: hidden;
        border-bottom: 1px solid $color-border--lightest;
        @include tr;
        @include flexColumn;

        .user-info__title {
            color: $color-text;
            font-size: 17px;
            font-weight: 500;
        } //.user-info__top .user-info__title
    } //.user-info__top

    &__bottom {
        position: absolute;
        right: -100%;
        bottom: 0;
        display: flex;
        justify-content: space-between;
        width: 100%;
        overflow: hidden;
        border-top: 1px solid $color-border--lightest;
        background-color: $color-bg--light;
        @include tr;

        @include min(md) {
            position: relative;
            right: 0;
            padding: 10px;
            background-color: transparent;
        }

        .user-info__title {
            font-weight: 500;
            text-transform: uppercase;
        } //.user-info__title

        &.is-active {
            right: 0;
            @include tr;

            @include min(md) {
                padding: 10px;
            }
        }
    } //.user-info__bottom

    &__item {
        position: relative;
        display: flex;
        align-items: center;
        width: 100%;
        padding: 12.5px 35px;

        @include min(md) {
            padding: 20px;
        }

        &:hover {
            cursor: default;
        }

        &.has-hover {
            &:hover {
                background-color: $color-bg--hover;
                cursor: default;

                .user-info__inner {
                    margin-left: 32px;
                    transition: margin-left 0.5s;
                    background-color: $color-bg--hover;
                }

                .btn-icon {
                    left: 20px;
                }
            }
        } //.service-list__item.has-hover

        & + .user-info__item {
            border-top: 1px solid $color-border--lightest;
        }
    } //.user-info__item

    &__header {
        @include flexCenter;

        .user {
            @include flexCenter;

            &__img {
                width: 40px;
                height: 40px;
                margin-right: 15px;
            }

            &__title {
                color: $color-text;
                font-size: 16px;
                font-weight: 500;
            }
        } //.user-info__title
    } //.user-info__header

    &__details {
        display: flex;
        align-items: center;
        font-size: 14px;
    } //.user-info__details

    &__left {
        width: 70%;
        color: $color-text--light;
        font-size: 14px;
        font-style: italic;
        font-style: italic;
    } //.user-info__left

    &__right {
        margin-left: auto;
        color: $gray--dark;
        font-size: 16px;
        font-weight: 600;

        &--total {
            margin-left: auto;
            font-size: 18px;
            font-weight: 500;
            font-weight: 500;
        } //.user-info__right--price
    } //.user-info__right

    &__user {
        display: flex;
        align-items: center;

        .user {
            &__avatar {
                margin-right: 20px;
                margin-bottom: 0;
            } //.user__avatar

            &__img {
                width: 45px;
                height: 45px;
            } //.user__img
        }
    } //.user-info__user

    &__title {
        color: $color-accent;
        font-size: 15px;
        font-weight: 300;
    } //.user-info__title

    &__date {
        position: absolute;
        top: -50%;
        visibility: hidden;
        opacity: 0;
    } //.user-info__date

    &__note {
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 100%;
        padding: 11.5px 20px;

        @include min(md) {
            padding: 10px;
        }
    } //.user-info__desc

    &__btn {
        position: absolute;
        right: 15px;
        left: 15px;
        display: none;
        justify-content: space-between;
        width: 100%;
        height: 50px;
        padding: 0 20px;
        border-radius: 0;
        font-size: 17px;
        letter-spacing: 0.5px;
        line-height: 51px;

        @include min(md) {
            top: 100px;
            right: 15px;
            z-index: -1;
            width: auto;
            overflow: hidden;
            border-radius: $border-radius;
        }

        @include sm-block() {
            box-shadow: none;
        }

        @include xs-block() {
            padding: 0 10px;
            line-height: 50px;
        }

        &.is-active {
            top: 0;
            z-index: 1;
            display: flex;
            @include tr;
        }

        span {
            text-align: right;
        }
    } //.user-info__btn

    &.is-active {
        .user-info {
            &__item {
                &:hover {
                    cursor: default;
                }
            }

            &__date {
                top: 50px;
                visibility: visible;
                opacity: 1;
                @include tr;
            } //.user-info__date

            &__title {
                color: $color-text--light;
                font-style: italic;
                @include tr;
            } //.user-info__title

            &__header {
                margin-bottom: 27px;
            } //.user-info__user

            &__note {
                visibility: hidden;
                opacity: 0;
            }

            &__btn {
                top: 15px;
                z-index: 1;
                display: flex;
                @include tr;
            }
        } //.user-info
    } //.user-info__top.is-active

    &--confirmed {
        .user-info__title {
            color: $color-text;
        } //.user-info__title
    } //.user-info--confirmed

    &.is-fixed {
        position: fixed;
        top: 0;
        width: 384px;
    } //.user-info.is-fixed

    .fa-ruble-sign {
        margin-left: 5px;
        color: $color-icon;
        font-size: 12px;
    } //.user-info .fa-ruble-sign
} //.user-info

.info-box {
    width: 100%;
    margin-bottom: 30px;
    padding: 20px;
    border: 1px solid $color-border--lightest;

    &:hover {
        cursor: default;
    }

    &__title {
        margin-bottom: 8px;
        color: $gray--darkest;

        font-size: 18px;
    } //.service-box__title

    &__desc {
        font-size: 15px;
        font-weight: 300;
    } //.info-box__desc
} //.info-box

.modal-order {
    .modal {
        &-dialog {
            display: flex;
            justify-content: center;
        } //.modal-order .modal-dialog

        &-header {
            align-items: center;

            .btn {
                margin-left: auto;
                font-weight: 300;
            }
        } //.modal-order .modal-header

        &-content {
            @include min(md) {
                max-width: 375px;
            }
        } //.modal-order .modal-content

        &-body {
            &__scroll-wrap {
                max-height: 420px;
                @include scroll;
                // overflow-y: auto;
                // @include bb-scroll--thin;
            } //.modal-order .modal-body__scroll-wrap
        }

        &-footer {
            display: flex;
            flex-shrink: 0;
            justify-content: center;
            margin-top: auto;
            background-color: transparent;

            .btn {
                padding: 3px 35px;
                font-size: 15px;
            }
        } //.modal-order .modal-footer
    } //.modal-order .modal

    .btn-link {
        padding: 0;
    } //.modal-order .btn-link

    .bb-checkbox {
        &__box {
            margin-bottom: 5px;
        }

        &--master {
            display: flex;
            align-items: center;
            margin-right: 0;

            @include xs-block() {
                height: 65px;
            }
        } //.modal-order .bb-checkbox--master
    } //.modal-order .bb-checkbox
} //.modal-order
